import './App.css';
import {useState} from "react";

// eslint-disable-next-line no-undef

function Modal({modalTitle, setModalTitle}) {
    return (
        <>
            <div className='modal'>
                <h4>{modalTitle}</h4>
                <p></p>
                <p></p>
                <button onClick={() => setModalTitle('  ')}> </button>
            </div>
            <div></div>
        </>
    );
}
function App() {

    let [list, setList] = useState([
        {title: '  ', cnt: 0},
        {title: '  ', cnt: 0},
        {title: '  ', cnt: 0}
    ])

    const clickCnt = (index) => {
        let copy = [...list]
        copy[index].cnt = copy[index].cnt + 1
        setList(copy)
    }
    let [modal, setModal] = useState(false)
    let [modalTitle, setModalTitle] = useState('')


    const openModal = (item) => {
        setModalTitle(item.title)
        modal ? setModal(false) : setModal(true)
    }

    let [newTitle, setNewTitle] = useState('')
    const postTitle = () => {
        if(newTitle) setList([{title: newTitle, cnt: 0}, ...list])
    }

    const deleteItem = (idx) => {
        let copy = [...list]
        copy.splice(idx, 1)
        setList(copy)
    }


    return (

        <div className="App">
            <div className="black_nav">
                ReactBlog
            </div>
            {
                list.map((listItem, index) => {
                    return (
                        <div className='list' key={index}>
                            <h4 onClick={() => {
                                openModal(listItem)
                            }}>
                                {listItem['title']}
                                <span onClick={(e) => {
                                    e.stopPropagation()
                                    clickCnt(index)
                                }}></span> {listItem['cnt']}
                            </h4>
                            <p>2 17 </p>
                            <button onClick={(_)=> {deleteItem(index)}}></button>
                        </div>
                    )
                })
            }
            <input
                type="text"
                onChange={
                    (e) => {
                        setNewTitle(e.target.value)
                    }
                }/>
            <button onClick={postTitle}> </button>
            {
                modal ? <Modal modalTitle={modalTitle} setModalTitle={setModalTitle}/> : null
            }
        </div>
    );
}

export default App;
